
body {
  font-family: "Roboto", sans-serif;
  position: absolute;
  height: 100%;
  width: 100%;
  margin: 0;
}
#app {
  height: 100%;
}
h1 {
  font-size: 20px;
  line-height: 28px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  margin: 0;
}
.subtitle {
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: 13px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.55);
  display: block;
}
.title-container {
  margin-bottom: 32px;
}


.navbar {
  -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.26);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.26);
  position: fixed;
  background: #fff;
  width: 100%;
  height: 76px;
  top: 0;
}
.content {
  height: 100%;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
aside {
  width: 300px;
  border-right: 1px solid rgba(0, 0, 0, 0.12);
  margin-top: 76px;
  height: calc(100% - 76px);
}
main {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background-color: #f4f4f4;
  height: 100%;
  overflow: auto;
  margin-top: 76px;
  height: calc(100% - 76px);
}
.card {
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  padding: 24px;
  background-color: #ffffff;
  margin-bottom: 16px;
}
.facets {
  list-style: none;
  margin: 0;
}
.facets li label {
  padding: 0 16px;
  font-size: 12pt;
  display: block;
  cursor: pointer;
}
.sidebar-element {
  line-height: 24pt;
  font-size: 14pt;
  display: block;
  -webkit-transition: background-color 0.2s ease-in-out;
  transition: background-color 0.2s ease-in-out;
}
.sidebar-element:hover {
  background-color: #f0f1f2;
}
.expandable {
  cursor: pointer;
}
.expandable:before {
  content: "►";
  display: inline-block;
}
.expanded > .expandable:before {
  content: "▼";
  display: inline-block;
}
.collapsed > .facets,
.collapsed > div {
  display: none;
}
.offset-left-0 .sidebar-element,
.offset-left-0 .facets {
  padding-left: 16px;
}
.offset-left-1 .sidebar-element,
.offset-left-1 .facets {
  padding-left: 32px;
}
.input-container {
  padding: 8px;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
.input-container .datepicker {
  display: inline-block;
}
.input-container input {
  padding: 1.25rem;
  background-color: #fff;
  border: 1px solid #e8e9ed;
  font-size: 1rem;
  display: inline-block;
  border: 1px solid #e8e9ed;
}
.graph-container {
  padding: 16px;
}

